@import 'styles/variables';
@import 'styles/mixins';
@import 'styles/utility';
@import 'styles/buttons';

$width-default: 430px;

.vault-info-banner {
  margin-top: 15px;
  margin-bottom: 15px;
  width: $width-default - 30px;
  color: $gray-shade-5;
  width: $width-default;
  margin: inherit;
  background-color: $white;
  box-shadow: none;
  bottom: initial;
  z-index: 3;
  max-height: inherit !important;
  height: 175px;

  .dialogContent {
    padding: 0;
    overflow: hidden;
    position: relative;

    #contentText1,
    #contentText2 {
      padding-right: 12px;
    }
  }

  .vault-info {
    width: calc(100% - 70px);
    margin-top: 15px;
    margin-bottom: 15px;
  }

  @include font-sm();

  h1 {
    @include font-xm();
    margin-bottom: $margin-sm;
    margin-top: $margin-sm;
  }

  .main-icon {
    width: 75px;
    height: 75px;
  }

  .btn-close {
    width: 12px;
    height: 12px;
    cursor: pointer;
  }

  .btn-link {
    min-width: 100px;
  }

  .buttons {
    padding-left: 0;
  }

  &.banner {
    background-color: $orange;
    color: black;

    .vault-info {
      width: 100%;
      margin: 5px -20px;
    }

    #mainTitle {
      padding-top: 5px;
      text-align: center;
      width: 400px;
      margin: 0 0 0 30%;
    }

    .btn-container-sm {
      margin-right: 15px;
    }

    .btnClose {
      padding-top: 12px;
    }

    .btn-base {
      padding: 5px;
      max-width: 100px;
      color: black;
    }

    .btn-link {
      text-decoration: underline;
    }
    .btn-gray {
      background: rgba(255, 255, 255, .2);
    }

    .buttons {
      padding: 0;
      margin: 0;
    }
  }
}

.empty .vault-info-overrides #main {
  top: 215px;
}

.vault-info-overrides {
  #tools, #ad {
    &.middlePosition {
      top: 60px;
    }
  }
  .emptyVault {
    top: -155px;
    overflow: hidden;
  }

  #emptySites,
  #emptyNotes,
  #emptyFormFills,
  #emptySentShares,
  #emptySharedFolders,
  #emptyReceivedShares,
  #emptyEmergencyTrusted,
  #emptyEmergencyTrusting,
  #emptyCreditMonitoring
  {
    .emptyVault {
      top: 0;

      .tour {
        margin-top: 0;
        top: 0;
      }
    }
  }
}

.leftMenuItem {
  &.newFlag.active {
    color: $gray-shade-6;
    background-color: $white;
  }
}
